import CloseIcon from 'vue-material-design-icons/Close.vue';
import NcModal from '@nextcloud/vue/dist/Components/NcModal.js';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import AssistantTextProcessingForm from './AssistantTextProcessingForm.vue';
import RunningEmptyContent from './RunningEmptyContent.vue';
import ScheduledEmptyContent from './ScheduledEmptyContent.vue';
import { emit } from '@nextcloud/event-bus';
export default {
  name: 'AssistantTextProcessingModal',
  components: {
    ScheduledEmptyContent,
    RunningEmptyContent,
    AssistantTextProcessingForm,
    NcModal,
    NcButton,
    CloseIcon
  },
  props: {
    /**
     * If true, add the modal content to the Viewer trap elements via the event-bus
     */
    isInsideViewer: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    input: {
      type: String,
      default: ''
    },
    output: {
      type: String,
      default: ''
    },
    textProcessingTaskTypeId: {
      type: [String, null],
      default: null
    },
    showSyncTaskRunning: {
      type: Boolean,
      default: false
    },
    showScheduleConfirmation: {
      type: Boolean,
      required: true
    },
    actionButtons: {
      type: Array,
      default: () => []
    }
  },
  emits: ['cancel', 'submit'],
  data() {
    return {
      show: true,
      closeButtonTitle: t('assistant', 'Close'),
      closeButtonLabel: t('assistant', 'Close Blablador'),
      modalSize: 'normal'
    };
  },
  computed: {
    shortInput() {
      if (this.input.length <= 200) {
        return this.input;
      }
      return this.input.slice(0, 200) + '…';
    }
  },
  mounted() {
    if (this.isInsideViewer) {
      const elem = this.$refs.modal_content;
      emit('viewer:trapElements:changed', elem);
    }
  },
  methods: {
    onCancel() {
      this.show = false;
      this.$emit('cancel');
    },
    onSubmit(params) {
      // this.show = false
      this.$emit('submit', params);
    },
    onSyncSubmit(params) {
      this.$emit('sync-submit', params);
    },
    onCancelNSchedule() {
      this.$emit('cancel-sync-n-schedule');
    },
    onActionButtonClicked(data) {
      this.$emit('action-button-clicked', data);
    }
  }
};